import { USER_API } from "@/api/client";
import { mapState } from "vuex";
import trading from "@/components/trading";
export default {
  data() {
    return {
      historyList: [],
      refreshLoading: false,
      start: 1,
      isLoading: true,
      loading: false,
      tradingSummarys: {
        totalReward: 0,
        totalTradingVolume: 0
      },
    };
  },
  components: {
    tradingCom: trading,
  },
  computed: {
    ...mapState(["local"]),
    isBitKeep() {
      return this.local.UA.isBitKeep;
    },
  },
  mounted() {
    this.isBitKeep &&
      BitKeepInvoke.onLoadReady(() => {
        BitKeepInvoke.setTitle(this.$t("trading.historyData"));
        BitKeepInvoke.setIconAction("");
        BitKeepInvoke.appMode((err, res) => {
          let body = document.getElementsByTagName("body")[0];
          if (res == 1) {
            body.setAttribute("class", "theme-dark");
          } else {
            body.setAttribute("class", "theme-light");
          }
        });
      });
    this.getHistoryList();
    this.getTradingSummary();
  },
  methods: {
    async getHistoryList() {
      const { data, status } = await USER_API.tradingHistory({
        start: this.start,
        limit: 20
      });
      if (status == 1) {
        this.isLoading = false;
        return this.$dialog.alert({
          message: data,
          confirmButtonText: this.$t("staking.know"),
          confirmButtonColor: "$theme-light-colorPrimary",
        });
      }
      var moreList = data.list;
      this.finished = false;
      if(this.start == 1 ){
        this.historyList = [];
        this.historyList = moreList;
      }else{
        this.historyList.push(...moreList);
      }
      this.refreshLoading = false;
      this.loading = false;
      this.isLoading = false;
      this.$toast.clear();
      if (this.historyList.length >= data.totalRows) {
        this.finished = true;
      }
    },
    // 上拉加载请求方法
    onLoadList() {
      this.start++;
      this.finished = false;
      this.getHistoryList();
    },
    onDownRefresh() {
      this.start = 1;
      this.getHistoryList();
    },
    async getTradingSummary() {
      const { data, status } = await USER_API.tradingSummary();
      if (status == 1) {
        this.isLoading = false;
        return this.$dialog.alert({
          message: data,
          confirmButtonText: this.$t("trading.know"),
          confirmButtonColor: "#7524f9",
        });
      }
      this.tradingSummarys = data;
    },
    milliFormat(num) {
      return (
        num &&
        num
          .toString()
          .replace(/^\d+/g, (m) => m.replace(/(?=(?!^)(\d{3})+$)/g, ","))
      );
    },
  },
};
